import { Fragment } from 'react';
import { Link } from 'gatsby';
import styled from '@emotion/styled';
import { kebabCase } from 'lodash-es';

import AuthorItem from '@/components/AuthorItem.tsx';
import { colors } from '@/styles/colors.ts';
import { PostFullHeader, PostFullTitle } from '@/styles/shared.ts';
import config from '@/website-config.ts';

import type { Author } from '@/templates/post.tsx';

type Props = {
  tags: string[];
  excerpt: string;
  title: string;
  author: Author[];
  readingTime: {
    text: string;
  };
  datetime: string;
};

export default function PostHead({ tags, datetime, readingTime, author, excerpt, title }: Props) {
  return (
    <PostFullHeader className="post-full-header">
      <PostFullTags className="post-full-tags">
        {tags &&
          tags.length > 0 &&
          config.showAllTags &&
          tags.map((tag, idx) => (
            <Fragment key={tag}>
              {idx > 0 && <Fragment>, &nbsp;</Fragment>}
              <Link to={`/tags/${kebabCase(tag)}/`}>{tag}</Link>
            </Fragment>
          ))}
        {tags && tags.length > 0 && !config.showAllTags && (
          <Link to={`/tags/${kebabCase(tags[0])}/`}>{tags[0]}</Link>
        )}
      </PostFullTags>
      <PostFullTitle className="post-full-title">{title}</PostFullTitle>
      <PostFullCustomExcerpt className="post-full-custom-excerpt">{excerpt}</PostFullCustomExcerpt>
      <PostFullByline className="post-full-byline">
        <section className="post-full-byline-content">
          <AuthorListUl className="author-list">
            <AuthorItem key={author[0].name} author={author[0]} />
          </AuthorListUl>

          <section className="post-full-byline-meta">
            <div className="author-name">
              {author.map((author) => (
                <Link key={author.name} to={'/about'}>
                  {author.name}
                </Link>
              ))}
            </div>
            <div className="byline-meta-content">
              <time className="byline-meta-date" dateTime={datetime}>
                {datetime}
              </time>
              <span className="byline-reading-time">
                <span className="bull">&bull;</span>
                {readingTime.text}
              </span>
            </div>
          </section>
        </section>
      </PostFullByline>
    </PostFullHeader>
  );
}

const PostFullTags = styled.section`
  display: flex;
  align-items: center;
  justify-content: flex-start;
  font-size: 1.2rem;
  font-weight: 600;
  line-height: 1.4em;
  color: ${colors.midgrey};

  a {
    font-size: 1.2rem;
  }
`;

const PostFullCustomExcerpt = styled.p`
  margin: 20px 0 0;
  font-size: 2rem;
  font-weight: 360;
  line-height: 1.4em;
  color: var(--post-card-description);

  @media (max-width: 500px) {
    font-size: 1.9rem;
    line-height: 1.5em;
  }
`;

const PostFullByline = styled.div`
  display: flex;
  justify-content: space-between;
  margin: 35px 0 0;
  padding-top: 15px;
  border-top: 1px solid var(--post-full-border);

  .post-full-byline-content {
    display: flex;
    flex-grow: 1;
    align-items: flex-start;
  }

  .post-full-byline-content .author-list {
    justify-content: flex-start;
    padding: 0 12px 0 0;
  }

  .post-full-byline-meta {
    margin: 2px 0 0;
    font-size: 1.4rem;
    line-height: 1.2em;
    color: #768086;
    letter-spacing: 0.2px;
  }

  .post-full-byline-meta .author-name {
    margin: 0 0 3px;
    font-size: 1.4rem;
    font-weight: 500;
    line-height: 1.3em;
  }

  .post-full-byline-meta .author-name a {
    font-size: 13px;
    color: var(--post-full-meta-link);
  }

  .post-full-byline-meta .author-name a:hover {
    color: var(--post-full-meta-link-hover);
  }

  .post-full-byline-meta .byline-reading-time,
  .post-full-byline-meta .byline-meta-date {
    font-size: 13px;
  }

  .post-full-byline-meta .bull {
    display: inline-block;
    margin: 0 4px;
    opacity: 0.6;
  }
`;

const AuthorListUl = styled.ul`
  display: flex;
  flex-wrap: wrap;
  margin: 0 0 0 4px;
  padding: 0;
  list-style: none;
`;
