import { Fragment } from 'react';
import styled from '@emotion/styled';
import { GatsbyImage } from 'gatsby-plugin-image';

import type { IGatsbyImageData } from 'gatsby-plugin-image';

type Props = {
  imageSource: IGatsbyImageData;
  title: string;
  imageMeta: {
    photoAuthor?: string;
    photoVender?: string;
  };
};

export default function PostImage({ imageSource, title, imageMeta }: Props) {
  const [photoAuthorName, photoAuthorLink] = imageMeta.photoAuthor?.split('>>') || [
    undefined,
    undefined,
  ];
  const [photoVenderName, photoVenderLink] = imageMeta.photoVender?.split('>>') || [
    undefined,
    undefined,
  ];

  return (
    <PostFullImage>
      <div className="image-wrapper">
        <GatsbyImage image={imageSource} style={{ height: '100%' }} alt={title} />
      </div>

      {photoAuthorLink && (
        <PhotoAuthorSection>
          Photo by{' '}
          <a href={photoAuthorLink} target="_blank" rel="noreferrer">
            {photoAuthorName}
          </a>
          {photoVenderName && (
            <Fragment>
              {' '}
              on{' '}
              {photoVenderLink ? (
                <a href={photoVenderLink} target="_blank" rel="noreferrer">
                  {photoVenderName}
                </a>
              ) : (
                <span>{photoVenderName}</span>
              )}
            </Fragment>
          )}
        </PhotoAuthorSection>
      )}
    </PostFullImage>
  );
}

const PostFullImage = styled.figure`
  width: 80%;
  max-height: 800px;
  margin: 25px auto 100px;
  background-size: cover;

  .image-wrapper {
    overflow: hidden;
    width: 100%;
    border: 1px solid var(--image-border-color);
    border-radius: 8px;

    img {
      border-radius: 8px;
    }
  }

  @media (max-width: 1170px) {
    .image-wrapper {
      img {
        max-width: 1170px;
      }
    }
  }

  @media (max-width: 800px) {
    width: 100%;
    margin: 25px auto 60px;
  }
`;

const PhotoAuthorSection = styled.figcaption`
  max-width: 728px;
  margin: 10px auto;
  font-size: 13px;
  text-align: center;

  a,
  span {
    font-size: 13px;
  }
`;
